import Widget from "./widget";
var todoList = [];
function render() {
    Widget.getControl("todoList").reload(getData({ done: false }));
    Widget.getControl("doneList").reload(getData({ done: true }));
}
export function getData(option) {
    return todoList.filter(function (item) { return item.done === option.done; });
}
export function insertTodo() {
    var inputControl = Widget.getControl("todoInput");
    var value = inputControl.getValue();
    if (!value) {
        alert("값을 입력해주세요.");
        return;
    }
    todoList.push({
        id: crypto.randomUUID(),
        content: value,
        done: false,
    });
    inputControl.resetValue();
    inputControl.focus();
    render();
}
export function renderCheckBox(data) {
    var inputControl = new Widget.input({
        id: "checkbox",
        type: "checkBox",
        checked: data.done,
        onChange: function (e) {
            if (!(e.target instanceof HTMLInputElement))
                return;
            data.done = e.target.checked;
            render();
        },
    });
    return inputControl;
}
export function renderContent(data) {
    var spanContrl = new Widget.span({ id: data.id, textContent: data.content });
    return spanContrl;
}
export function renderDeleteBtn(data) {
    var delBtnContrl = new Widget.button({
        id: data.id,
        textContent: "삭제",
        onClick: function () {
            todoList.splice(todoList.indexOf(data), 1);
            render();
        },
    });
    return delBtnContrl;
}
